import React from "react";
import { Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const ChartExample = () => {
  const data = {
    labels: ["JAN", "FEB", "MAR", "APR", "MAY", "JUN", "JUL", "AUG"],
    datasets: [
      {
        label: "CHN",
        data: [15, 20, 25, 30, 35, 40, 45, 50], //  
        backgroundColor: (context) => {
          const chart = context.chart;
          const ctx = chart.ctx;
          const height = chart.canvas.height;
          const gradient = ctx.createLinearGradient(0, height, 0, 0);
          gradient.addColorStop(1, "#ffbf96"); //  ()
          gradient.addColorStop(0, "#fe7096"); //  ()
          return gradient;
        },
        barPercentage: 0.6, //   
        categoryPercentage: 0.4, //   
      },
      {
        label: "USA",
        data: [40, 10, 15, 20, 25, 30, 35, 45], //  
        backgroundColor: (context) => {
          const chart = context.chart;
          const ctx = chart.ctx;
          const height = chart.canvas.height;
          const gradient = ctx.createLinearGradient(0, height, 0, 0);
          gradient.addColorStop(1, "#90caf9"); //  ()
          gradient.addColorStop(0, "#047edf"); //  ()
          return gradient;
        },
        barPercentage: 0.6,
        categoryPercentage: 0.4,
      },
      {
        label: "UK",
        data: [25, 30, 35, 40, 45, 45, 20, 30], //  
        backgroundColor: (context) => {
          const chart = context.chart;
          const ctx = chart.ctx;
          const height = chart.canvas.height;
          const gradient = ctx.createLinearGradient(0, height, 0, 0);
          gradient.addColorStop(1, "#da8cff"); //  ()
          gradient.addColorStop(0, "#9a55ff"); //  ()
          return gradient;
        },
        barPercentage: 0.6,
        categoryPercentage: 0.4,
      },
    ],
  };

  const options = {
    indexAxis: "x", //  
    responsive: true,
    maintainAspectRatio: false, //     
    plugins: {
      title: {
        display: false,
        text: "Visit And Sales Statistics",
      },
      legend: {
        display: false, //   (    true )
        position: "top",
      },
    },
    scales: {
      x: {
        title: {
          display: false,
          text: "Month",
        },
        grid: {
          display: false, //     
        },
        border: {
          display: false, //    X  
        },
        ticks: {
          color: "#9c9fa6", //   
          font: {
            size: 12,       //   
            weight: "bold", //  
            family: "IRANSans, sans-serif", //   
            // style: "italic", //    ()
          },
          padding: 20, //     
        },
      },

      y: {
        display: true, //  Y   
        title: {
          display: false, //   Y  
        },
        ticks: {
          display: false, //     
          stepSize: (Math.max(...data.datasets.flatMap(d => d.data)) - 0) / 3, //  
          max: Math.max(...data.datasets.flatMap(d => d.data)), //   
          min: 0, //   ()
        },
        grid: {
          display: true, //      
          drawTicks: false, //      
          drawBorder: false, //    Y  
          tickLength: 0, //     
        },
        border: {
          display: false, //     Y  
        },
      },
    },
    layout: {
      padding: 0, //   padding 
    },
  };

  return <div style={{ height: "330px", width: "100%" }}>
  <Bar data={data} options={options} />
</div>


};

export default ChartExample;